// EventsList.js
import React, { useState, useEffect, useCallback } from 'react';
import ApiService from '../services/api.js';
import EventCard from './EventCard.js';
import EventCreateModal from './EventCreateModal.js';
import EventDetailsModal from './EventDetailsModal.js'; // Zaimportuj EventDetailsModal

// Funkcja pomocnicza do pobierania ID uytkownika z tokenu - BEZ HOOKW!
const getUserIdFromToken = () => {
    try {
        const token = localStorage.getItem('jwt_token') ||
            document.cookie.split('; ').find(row => row.startsWith('jwt_token='))?.split('=')[1];

        if (!token) {
            // console.log('Helper: No JWT token found'); // Logowanie moe by w komponencie
            return null;
        }
        const payload = JSON.parse(atob(token.split('.')[1]));
        const userIdStr = payload['http://schemas.xmlsoap.org/ws/2005/05/identity/claims/nameidentifier'];
        const result = userIdStr ? parseInt(userIdStr, 10) : null;
        return result;
    } catch (error) {
        console.error('Helper: Error decoding token or getting user ID:', error);
        return null; // Lub rzu bd, aby komponent go obsuy
    }
};

const EventsList = () => {
    const [events, setEvents] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [activeTab, setActiveTab] = useState('upcoming');
    const [showCreateModal, setShowCreateModal] = useState(false);
    const [currentUserId, setCurrentUserId] = useState(null); // ID zalogowanego uytkownika

    // Stany do zarzdzania EventDetailsModal
    const [showDetailsModal, setShowDetailsModal] = useState(false);
    const [selectedEventId, setSelectedEventId] = useState(null);

    useEffect(() => {
        console.log('EventsList: Component mounted, attempting to get user ID...');
        try {
            const userId = getUserIdFromToken(); // Wywoaj poprawion funkcj
            console.log('EventsList: User ID from token:', userId);
            setCurrentUserId(userId);
        } catch (err) {
            console.error('EventsList: Error processing user ID on mount:', err);
            setCurrentUserId(null);
        }
        // Pocztkowe pobranie wydarze, niezalenie od userId.
        // Mona dostosowa, jeli wydarzenia zale od obecnoci userId.
        fetchEvents('upcoming');
    }, []); // Pusta tablica zalenoci - uruchamia si raz po zamontowaniu

    const fetchEvents = useCallback(async (type = 'upcoming') => {
        console.log('EventsList: Fetching events of type:', type);
        setLoading(true);
        setError(null);
        try {
            let endpoint = '/api/events';
            if (type === 'upcoming') {
                endpoint = '/api/events/upcoming?count=20';
            } else if (type === 'past') {
                endpoint = '/api/events/past?count=20';
            } // Jeli type === 'all', uyje '/api/events'

            const data = await ApiService.get(endpoint);
            console.log('EventsList: Fetched events:', data?.length || 0);
            setEvents(data || []);
        } catch (err) {
            console.error('Error fetching events:', err);
            setError('Nie udao si zaadowa wydarze. Sprbuj ponownie.');
        } finally {
            setLoading(false);
        }
    }, []);

    const handleTabChange = (type) => {
        setActiveTab(type);
        fetchEvents(type);
    };

    const handleEventCreated = () => {
        setShowCreateModal(false);
        fetchEvents(activeTab); // Odwie aktywn zakadk
    };

    const handleEventDeleted = (deletedEventId) => {
        setEvents(prevEvents => prevEvents.filter(event => event.id !== deletedEventId));
        // Opcjonalnie zamknij modal szczegw, jeli usunite wydarzenie byo wywietlane
        if (selectedEventId === deletedEventId) {
            closeDetailsModal();
        }
    };

    const handleRegistrationChange = () => {
        // Ta funkcja jest wywoywana przez EventDetailsModal po rejestracji/wyrejestrowaniu.
        // Moemy chcie ponownie pobra szczegy konkretnego wydarzenia, list uczestnikw,
        // lub ca list wydarze, jeli np. licznik uczestnikw jest na kartach.
        // Dla uproszczenia, pobierzmy ponownie aktualn list.
        console.log('EventsList: Registration changed, re-fetching events for tab:', activeTab);
        fetchEvents(activeTab);
    };

    // Funkcje do kontrolowania EventDetailsModal
    const openDetailsModal = (eventId) => {
        console.log('EventsList: Opening details modal for event ID:', eventId);
        setSelectedEventId(eventId);
        setShowDetailsModal(true);
    };

    const closeDetailsModal = () => {
        console.log('EventsList: Closing details modal.');
        setShowDetailsModal(false);
        setSelectedEventId(null);
    };

    if (loading && events.length === 0) {
        return (
            <div className="container mt-4">
                <div className="d-flex justify-content-center py-5">
                    <div className="spinner-border text-success" role="status" style={{width: "3rem", height: "3rem"}}>
                        <span className="visually-hidden">adowanie wydarze...</span>
                    </div>
                </div>
            </div>
        );
    }

    return (
        <div className="container mt-4">
            {/* Nagwek */}
            <div className="row mb-4">
                <div className="col">
                    <div className="d-flex justify-content-between align-items-center">
                        <div>
                            <h1 className="mb-1">
                                <i className="bi bi-calendar-event me-2 text-success"></i>
                                Wydarzenia
                            </h1>
                            <p className="text-muted">Odkryj lokalne wydarzenia ekologiczne w Twojej okolicy</p>
                        </div>
                        {currentUserId && (
                            <button
                                className="btn btn-success btn-lg"
                                onClick={() => setShowCreateModal(true)}
                            >
                                <i className="bi bi-plus-circle me-2"></i>
                                Dodaj Wydarzenie
                            </button>
                        )}
                    </div>
                </div>
            </div>

            {/* Zakadki nawigacyjne */}
            <div className="row mb-4">
                <div className="col">
                    <ul className="nav nav-pills justify-content-center">
                        <li className="nav-item">
                            <button
                                className={`nav-link ${activeTab === 'upcoming' ? 'active' : ''}`}
                                onClick={() => handleTabChange('upcoming')}
                            >
                                <i className="bi bi-calendar-plus me-1"></i>
                                Nadchodzce
                            </button>
                        </li>
                        <li className="nav-item">
                            <button
                                className={`nav-link ${activeTab === 'all' ? 'active' : ''}`}
                                onClick={() => handleTabChange('all')}
                            >
                                <i className="bi bi-calendar3 me-1"></i>
                                Wszystkie
                            </button>
                        </li>
                        <li className="nav-item">
                            <button
                                className={`nav-link ${activeTab === 'past' ? 'active' : ''}`}
                                onClick={() => handleTabChange('past')}
                            >
                                <i className="bi bi-calendar-check me-1"></i>
                                Przesze
                            </button>
                        </li>
                    </ul>
                </div>
            </div>

            {/* Komunikat bdu */}
            {error && (
                <div className="alert alert-danger d-flex align-items-center mb-4">
                    <i className="bi bi-exclamation-triangle-fill me-2"></i>
                    {error}
                    <button className="btn btn-outline-danger btn-sm ms-auto" onClick={() => fetchEvents(activeTab)}>
                        <i className="bi bi-arrow-clockwise me-1"></i>
                        Sprbuj ponownie
                    </button>
                </div>
            )}

            {/* Siatka wydarze */}
            {events.length > 0 ? (
                <div className="row g-4">
                    {events.map(event => (
                        <div key={event.id} className="col-md-6 col-lg-4">
                            <EventCard
                                event={event}
                                currentUserId={currentUserId}
                                // Przeka funkcj do otwierania modala do EventCard
                                onOpenDetails={() => openDetailsModal(event.id)}
                            />
                        </div>
                    ))}
                </div>
            ) : (
                <div className="text-center py-5">
                    <i className="bi bi-calendar-x text-muted" style={{fontSize: '4rem'}}></i>
                    <h3 className="mt-3 text-muted">Brak wydarze</h3>
                    <p className="text-muted">
                        {activeTab === 'upcoming' && 'Nie ma nadchodzcych wydarze.'}
                        {activeTab === 'past' && 'Nie ma przeszych wydarze.'}
                        {activeTab === 'all' && 'Nie ma adnych wydarze.'}
                    </p>
                    {currentUserId && (
                        <button
                            className="btn btn-success mt-3"
                            onClick={() => setShowCreateModal(true)}
                        >
                            <i className="bi bi-plus-circle me-2"></i>
                            Stwrz pierwsze wydarzenie
                        </button>
                    )}
                </div>
            )}

            {/* Nakadka adowania podczas odwieania */}
            {loading && events.length > 0 && (
                <div className="position-fixed top-50 start-50 translate-middle" style={{zIndex: 1050}}>
                    <div className="spinner-border text-success" role="status">
                        <span className="visually-hidden">Odwieanie...</span>
                    </div>
                </div>
            )}

            {/* Modal tworzenia wydarzenia */}
            <EventCreateModal
                show={showCreateModal}
                onHide={() => setShowCreateModal(false)}
                onEventCreated={handleEventCreated}
            />

            {/* Modal szczegw wydarzenia - teraz renderowany i kontrolowany przez EventsList */}
            {showDetailsModal && selectedEventId && (
                <EventDetailsModal
                    show={showDetailsModal}
                    onHide={closeDetailsModal}
                    eventId={selectedEventId}
                    currentUserId={currentUserId}
                    onEventDeleted={() => {
                        // Po usuniciu wydarzenia z modala, aktualizujemy list i zamykamy modal
                        handleEventDeleted(selectedEventId); // selectedEventId to ID aktualnie wywietlanego wydarzenia
                        // closeDetailsModal(); // handleEventDeleted moe ju to robi, jeli jest taka potrzeba
                    }}
                    onRegistrationChange={handleRegistrationChange} // Przeka t funkcj
                />
            )}
        </div>
    );
};

export default EventsList;