// EventsPage.js
import React, { StrictMode } from 'react';
import ReactDOM from 'react-dom/client';
import EventsList from './EventsList.js';

console.log('EventsPage: Starting initialization...');

const eventsContainer = document.getElementById('react-events-page');
if (eventsContainer) {
    console.log('EventsPage: Container found, creating React root...');

    // Usunito prb pobrania currentUserId tutaj,
    // zakadajc, e EventsList lub globalny kontekst auth sobie z tym radzi.
    // Jeli EventsList potrzebuje ID z data-atrybutu, przywr poprzedni logik,
    // ale upewnij si, e serwer poprawnie ustawia ten atrybut.
    // Na podstawie logw "EventsList: Current user ID: 7", wydaje si, e EventsList ma swoje rdo.

    const root = ReactDOM.createRoot(eventsContainer);
    root.render(
        <StrictMode>
            {/* Jeli EventsList sam pobiera ID, nie trzeba go tu przekazywa. */}
            {/* Jeli jednak EventsList oczekuje 'currentUserId' jako prop,
                a poprzednio byo 'null', to trzeba znale, skd EventsList bierze '7'
                i upewni si, e ten mechanizm jest stabilny. */}
            <EventsList />
            {/* LUB, jeli EventsList oczekuje go jako prop, ale my go tu nie mamy:
            <EventsList currentUserId={eventsContainer.dataset.currentUserId || null} />
            Wtedy musisz naprawi ustawianie data-current-user-id po stronie serwera.
            Jednak logi wskazuj, e EventsList i tak znajduje '7'.
            */}
        </StrictMode>
    );
    console.log('Events page initialized');
} else {
    console.warn('Events container not found - component will not be rendered');
}